/* ------------------ GLOBAL ------------------ */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #020617;
  color: #e5e7eb;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

/* ------------------ NAVBAR ------------------ */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #020617;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 70px;
  border-bottom: 1px solid #0f172a;
  z-index: 1000;
}

.logo {
  font-size: 27px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-weight: 700;
  color: #38bdf8;

}
.nav-links {
  list-style: none;
  display: flex;
  gap: 30px;
}

.nav-links a {
  text-decoration: none;
  color: #e5e7eb;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #2eb1b6;
}

.logo {
  color: #00c6ff;
  text-shadow: 0 0 8px #00c6ff;
  font-size: 27px;
  letter-spacing: 1px;
}

.navbar a {
  color: #ffffff;
  transition: 0.3s;
}

.navbar a:hover {
  color: #00c6ff;
  text-shadow: 0 0 6px #00c6ff;
}
.navbar {
  background: #020617;
  border-bottom: 2px solid;
  border-image: linear-gradient(to right, #00c6ff, #7c7cff);
  border-image-slice: 1;
}


/* ------------------ HOME ------------------ */
.hero {
  height: 87vh;
  background: #020617;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
  padding-right: 700px;
  text-align: center;
  font-family: "Poppins', sans-serif";
}
.hero content {
  max-width: 600px;
}

/* HELLO */
.hello {
  color: #f5b301;
  letter-spacing: 4px;
  font-weight: 600;
  margin-bottom: 20px;
}

/* MAIN NAME */
.title {
  font-size: 4rem;
  font-weight: 600;
  margin-bottom: 20px;
}

.title span {
  color: #f5b301;
  font-weight: bolder;
}


/* ROLE */
.role {
  font-size: 1.3rem;
  opacity: 0.8;
  margin-bottom: 40px;
}

/* BUTTON */
.hero-btn {
  padding: 14px 40px;
  border: 1px solid #fff;
  border-radius: 30px;
  text-decoration: none;
  color: #fff;
  font-size: 0.9rem;
  letter-spacing: 2px;
  transition: 0.3s;
} 
.hero-btn:hover {
  background: #f5b301;
  color: #000;
  border-color: #f5b301;
}


.typing-text {
  font-size: 2rem;
  font-weight: 600;
  margin-top: 20px;
  margin-bottom: 20px;
  color: #ffffff;
}

.typing-text span {
  color: #f3f2f0;
  font-weight: 500;
}



/* ------------------ SECTIONS ------------------ */
.section {
  padding: 90px 70px;
}

.section h2 {
  font-size: 40px;
  margin-bottom: 20px;
  color: #38bdf8;
}

.section p {
  font-size: 18px;
  max-width: 900px;
  line-height: 1.8;
}

/*--------------------About---------------------*/

.about-section {
  background: #020617;
  color: #fff;
  padding: 80px 10%;
  font-family: 'Poppins', sans-serif;
}

.about-container {
  display: flex;
  gap: 80px;
}

/* LEFT */
.about-left {
  width: 40%;
}

.profile-img {
  width: 200px;
  height: auto;
  object-fit: contain;
  filter: drop-shadow(0 0 25px rgba(255, 193, 7, 0.35));
  transition: transform 0.4s ease;
  margin-bottom: 30px;
}
/* Hover effect (optional) */
.profile-img:hover {
  transform: scale(1.05);
}

.info p {
  color: #aaa;
  margin-bottom: 8px;
  font-size: 20px;
}

.info span {
  color: #fff;
  font-weight: 600;
}



/* RIGHT */
.about-right {
  width: 60%;
}

.about-right h1 {
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 20px;
  color:#00c6ff;
}

.about-text {
  color: #aaa;
  line-height: 1.8;
  margin-bottom: 30px;
  font-size: 18px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.details p {
  margin-bottom: 12px;
  color: #ccc;
}

.details strong {
  color: #fff;
}

/* ------------------ Education ------------------ */
.education-section {
  background: #020617;
  color: #fff;
  padding: 80px 8%;
}

.education-title {
  text-align: center;
  font-size: 48px;
  color: #00c6ff;
  margin-bottom: 15px;
}

.education-line {
  width: 60%;
  height: 3px;
  background: linear-gradient(to right, #cfa32b, #6b5b1e);
  margin: 0 auto 50px;
}

/* GRID */
.education-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 38px;
  position: relative;
  width: 100%;
  padding-right: 50px;
  padding-left: 50px;
}

/* CARD */
.education-card {
  background: #171f3d;
  padding: 50px;
  border-radius: 6px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.6);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.education-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 35px rgba(255, 193, 7, 0.15);
}

/* YEAR */
.edu-year {
  display: block;
  font-size: 30px;
  font-weight: 1000;
  color: #ffc107;
  margin-bottom: 15px;
}

/* DEGREE */
.education-card h3 {
  font-size: 22px;
  margin-bottom: 10px;
}

/* UNIVERSITY */
.edu-university {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 13px;
  color: #b0b0b0;
  margin-bottom: 20px;
}

/* GRADE */
.edu-grade {
  font-size: 15px;
  color: #cfcfcf;
}




/* ------------------ PROJECTS ------------------ */

.projects-section {
  background: #020617;
  padding: 90px 8%;
  color: #fff;
}

.projects-title {
  text-align: center;
  color: #00c6ff;
  font-size: 48px;
  font-weight: 700;
}

.projects-subtitle {
  text-align: center;
  color: #aaa;
  margin: 12px 0 60px;
  font-size: 16px;
}

/* GRID */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 35px;
}

/* CARD */
.project-card {
  background: #171f3d;
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

.project-card:hover {
  transform: translateY(-12px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.8);
}

/* IMAGE */
.project-card img {
  width: 100%;
  height: 220px;
  object-fit: cover;
}

/* TEXT */
.project-card h3 {
  font-size: 20px;
  margin: 20px;
}

.project-card p {
  font-size: 14px;
  color: #ccc;
  margin: 0 20px 25px;
  line-height: 1.6;
}

/* RESPONSIVE */
@media (max-width: 1000px) {
  .projects-grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 650px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }
}


.github-section {
  width: 100%;
  height: 300px;

  background: #020617; /* dark background */
  flex-direction: column;
  justify-content: center;  /* horizontal center */
  align-items: center;      /* vertical center */
  color: white;
}

/* center content */
.github-content {
  text-align: center;
}

/* heading */
.github-content h2 {
  font-size: 42px;
  font-weight: 700;
  margin: 0 0 25px 0;
  margin-bottom: 25px;
  
}

.github-content h2 span {
  color: #ffb703;
}

/* button */
.github-btn {
  display: inline-block;
  padding: 14px 36px;
  background-color: #ffb703;
  color: #000;
  font-weight: 700;
  border-radius: 30px;
  text-decoration: none;
  transition: 0.3s ease;
}

.github-btn:hover {
  background-color: #ffc933;
  transform: translateY(-3px);
}




/* ------------------ CONTACT ------------------ */
.contact-section {
  background: #020617;
  color: white;
 
  text-align: center;
}

.contact-title {
  font-size: 52px;
  font-weight: 700;
  margin-bottom: 10px;
}

.contact-subtitle {
  color: #888;
  margin-bottom: 70px;
}

/* Cards */
.contact-cards {
  display: flex;
  justify-content: center;
  gap: 80px;
  flex-wrap: wrap;
}

.contact-card {
  width: 250px;
}

.icon-circle {
  width: 90px;
  height: 90px;
  background: #333e71;
  border-radius: 50%;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  color: #ffb703;
}

.contact-link {
  color: #aaa;
  font-size: 15px;
  text-decoration: none;
  transition: color 0.3s ease;
}

.contact-link:hover {
  color: #ffb703;
  text-decoration: underline;
}

.contact-card h3 {
  font-size: 16px;
  letter-spacing: 1px;
  margin-bottom: 10px;
}

.contact-card p,
.contact-card a {
  color: #aaa;
  font-size: 15px;
  text-decoration: none;
}

/* CTA */
.contact-cta {
  font-family: "Poppins", Arial, sans-serif;
  margin-top: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}

.contact-cta h2 {
  font-size: 32px;
  font-weight: 400;
}

.cta-btn {
  font-family: "Poppins", Arial, sans-serif;
  background: #ffb703;
  color: black;
  padding: 14px 36px;
  border-radius: 30px;
  font-weight: 700;
  text-decoration: none;
}

/* Social Icons */
.social-icons {
  margin-top: 60px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.social-icons a {
  width: 45px;
  height: 45px;
  background: #111;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffb703;
  margin-bottom: 20px;
  font-size: 20px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* LinkedIn */
.social-icons .linkedin {
  background-color: #0A66C2;
  color: white;
}

/* Instagram */
.social-icons .instagram {
  background-color: #E1306C;
  color: white;
}

/* GitHub */
.social-icons .github {
  color: #ffffff;
}

/* Hover effect */
.social-icons a:hover {
  transform: translateY(-4px);
  box-shadow: 0 0 15px rgba(255, 183, 3, 0.4);
}

/* ------------------ FOOTER ------------------ */
.footer {
  text-align: center;
  padding: 20px;
  background-color: #020617;
  border-top: 1px solid #0f172a;
  font-size: 14px;
  color: #9ca3af;
}

/* ------------------ RESPONSIVE ------------------ */
@media (max-width: 768px) {
  .navbar {
    padding: 15px 20px;
    flex-direction: column;
    gap: 10px;
  }

  .nav-links {
    flex-wrap: wrap;
    justify-content: center;
  }

  .home h1 {
    font-size: 38px;
  }

  .section {
    padding: 70px 20px;
  }
}
