.skills-section {
  background: #020617;
  color: #fff;
  padding: 80px 20px;
  width: 100%;
}
.skills-section {
  padding: 60px 40px;
  background: radial-gradient(circle at top, #020617);
  color: white;
  text-align: center;
}

.skills-section h2 {
  color: #00d9ff;
  margin-bottom: 20px;
  font-size: 48px;
}

.skills-section h3 {
  color: #a17a06;
  margin: 40px 0 20px;
  font-size: 24px;
}

/* ðŸ”¥ KEY FIX: Grid auto-wrap + center */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  gap: 30px;
  justify-items: center;
}

/* Skill Card */
.skill-card {
  text-align: center;
}


/* Hexagon Shape */
.hexagon {
  width: 110px;
  height: 110px;
  background: linear-gradient(135deg, #00c6ff, #0072ff);
  clip-path: polygon(
    25% 0%,
    75% 0%,
    100% 50%,
    75% 100%,
    25% 100%,
    0% 50%
  );
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 25px rgba(0, 198, 255, 0.6);
  transition: transform 0.3s ease;
}

.hexagon svg {
  font-size: 45px;
  color: unset;
}

.hexagon:hover {
  transform: translateY(-8px) scale(1.05);
}

/* Grid */
.skills-grid {
  display: flex !important;
  flex-direction: row !important;
  flex-wrap: wrap !important;
  justify-content: center !important;
  align-items: center !important;
  gap: 40px;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  width: 100%;
}


/* Hex Card */
.hex-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 120px;
  
}

/* Hexagon Shape */
.hex-shape {
  width: 90px;
  height: 52px;
  background: linear-gradient(135deg, #00c6ff, #0072ff);
  position: relative;
  margin: 26px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 20px rgba(0, 198, 255, 0.6);
  transition: transform 0.3s ease;
}

.hex-shape::before,
.hex-shape::after {
  content: "";
  position: absolute;
  width: 0;
  border-left: 45px solid transparent;
  border-right: 45px solid transparent;
}

.hex-shape::before {
  bottom: 100%;
  border-bottom: 26px solid #00c6ff;
}

.hex-shape::after {
  top: 100%;
  border-top: 26px solid #0072ff;
}

/* Icon */
.hex-icon {
  font-size: 34px;
}

/* Hover Effect */
.hex-shape:hover {
  transform: scale(1.1);
}

/* Text */
.hex-card p {
  margin-top: 10px;
  font-size: 14px;
  color: #ddd;
}
