.experience-section {
  background: radial-gradient(circle at top, #020617);
  padding: 80px 20px;
  color: white;
  text-align: center;
}

.experience-section h2 {
  color: #00d9ff;
  font-size: 48px;
  margin-bottom: 60px;
}

/* Timeline */
.timeline {
  position: relative;
  max-width: 1000px;
  margin: auto;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(#00d9ff, #0072ff);
  transform: translateX(-50%);
  box-shadow: 0 0 20px #00d9ff;
}

/* Timeline Item */
.timeline-item {
  position: relative;
  width: 50%;
  padding: 30px 40px;
}

.timeline-item.left {
  left: 0;
  text-align: right;
}

.timeline-item.right {
  left: 50%;
  text-align: left;
}

/* Icon */
.timeline-icon {
  position: absolute;
  top: 30px;
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #00c6ff, #0072ff);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 20px rgba(0, 198, 255, 0.8);
  z-index: 10;
}

.timeline-item.left .timeline-icon {
  right: -25px;
}

.timeline-item.right .timeline-icon {
  left: -25px;
}

.timeline-icon svg {
  font-size: 22px;
  color: white;
}

/* Content Card */
.timeline-content {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 0 25px rgba(0, 198, 255, 0.15);
}

.timeline-content h3 {
  color: #00d9ff;
  margin-bottom: 5px;
   text-align: left;
}

.timeline-content h4 {
  color: #ffc107;
  margin-bottom: 5px;
  text-align: left;
}

.timeline-content span {
  font-size: 13px;
  color: #aaa;
  text-align: left;
}

.timeline-content ul {
  margin-top: 10px;
  padding-left: 20px;
}

.timeline-content li {
  font-size: 14px;
  margin-bottom: 6px;
   text-align: left;
}

/* Mobile */
@media (max-width: 768px) {
  .timeline::before {
    left: 20px;
  }

  .timeline-item {
    width: 100%;
    padding-left: 60px;
    padding-right: 20px;
    text-align: left;
  }

  .timeline-item.left,
  .timeline-item.right {
    left: 0;
  }

  .timeline-icon {
    left: 0;
  }
}
