.linkedin-btn {
  margin-top: 15px;
  display: inline-flex;
  align-items: center;
  gap: 8px;

  padding: 8px 18px;
  font-size: 20px;
  font-weight: 600;

  background: #ffbf2f;
  color: #000;
  border-radius: 25px;

  text-decoration: none;
  width: fit-content;

  transition: all 0.3s ease;
}

.linkedin-btn:hover {
  background: #ffcc4d;
  transform: translateY(-2px);
}

.linkedin-icon {
  font-size: 16px;
}
